          balance: 1000,
          avatarId: this.user?.avatar_id,
          chatId: this.user.chat_id
        };

        const room = await this.client.joinOrCreate('poker_room', options);


        /*
        const room = await this.client.joinById(roomId, {
          name: this.username,
          balance: 1000
        });
        */

