    runTickTimerFindGame(min) {
      return new Promise((resolver) => {
        if (min < 10) {
          this.timerFindGameClock = "0" + min + ":00";
        } else {
          this.timerFindGameClock = min + ":00";
        }

        let timerSec = min * 60;
        this.intervalTimerFindGame = setInterval(() => {
          timerSec--;
          const time = timerSec / 60;
          const minutes = Math.trunc(time);
          let minutesFormat;
          if (minutes < 10) {
            minutesFormat = "0" + minutes;
          } else {
            minutesFormat = minutes;
          }
          const seconds = Math.round((time - minutes) * 60);
          let secondsFormat;
          if (seconds < 10) {
            secondsFormat = "0" + seconds;
          } else {
            secondsFormat = seconds;
          }
          this.timerFindGameClock = [minutesFormat, secondsFormat].join(':');

          if (timerSec === 0) {
            clearInterval(this.intervalTimerFindGame);
            resolver(true);
          }

        }, 1000);
      });
    },
    trans(code) {
      const lang = this.user.lang || 'ru';
      if (!this.lang[lang]) return '';
      if (!this.lang[lang][code]) return '';
      return this.lang[lang][code];
    },
    onInput() {
      this.runProcessCheckNickname();
    },
    onKeyUp(event) {
      //if (['Delete', 'Backspace'].includes(event.key)) {
      //this.runProcessCheckNickname();
      //}
    },
    onKeypressNickname(event) {
      const allowed = /[A-Za-z0-9]/g;
      const key = String.fromCharCode(event.keyCode);
      if (!allowed.test(key)) {
        event.preventDefault();
        return false;
      }
    },
    onKeypressBet(event) {
      const allowed = /[0-9.]/g;
      const key = String.fromCharCode(event.keyCode);

      //   
      if (!allowed.test(key)) {
        event.preventDefault();
        return false;
      }

      const prevValue = String(this.inputValue);
      const isPrevValueHasDot = String(this.inputValue).includes('.');
      //   
      if (key === '.') {
        if (isPrevValueHasDot) {
          event.preventDefault();
          return false;
        }
      }
      //,        
      if (isPrevValueHasDot) {
        const arr = prevValue.split('.');
        if (arr[1].length === 1) {
          event.preventDefault();
          return false;
        }
      }

    },
    checkBet() {
      if (parseFloat(this.inputValue) > this.maxInputValue) {
        this.inputValue = this.maxInputValue;
        this.inputValueSlider = this.maxInputValue;
      }
      if (parseFloat(this.inputValue) < this.minInputValue) {
        this.inputValue = this.minInputValue;
        this.inputValueSlider = this.minInputValue;
      }
    },
    runProcessCheckBet() {
      if (!this.debounceFunc) {
        this.debounceFunc = this.debounce(this.checkBet.bind(this), 1000);
      }
      this.debounceFunc();
    },
    runProcessCheckNickname() {
      if (this.nickname.length < 5) {
        this.isValidNickname = undefined;
        return;
      }
      if (!this.debounceFunc) {
        this.debounceFunc = this.debounce(this.checkNickname.bind(this), this.checkNicknameDebounceMsec);
      }
      this.debounceFunc();
    },
    checkNicknameOnServer() {
      return new Promise(async (resolver) => {
        try {
          const str = [this.chatId + this.nickname + SALT].join('');
          const secure = SHA256(str).toString();

          const {data} = await axios.post(
              CONSTANTS.API_URL + "/user/check-username", {
                chat_id: this.chatId,
                nickname: this.nickname,
                secure: secure
              });

          resolver(data);
        } catch (e) {
          resolver(false);
        }
      });
    },
    async checkNickname() {
      //  
      this.isValidNickname = undefined;
      this.isLoadingNickname = true;
      const data = await this.checkNicknameOnServer();
      this.isValidNickname = !!(data?.result?.status);
      this.isLoadingNickname = false;
