    async submit() {
      if (this.isLoadingSaveData) return;
      if (!this.isValidForm()) {
        return;
      }
      this.isLoadingSaveData = true;
      this.showSpinner();
      const response = await this.saveUserData();
      await this.sleepSec(1);
      if (response?.result?.status) {
        this.user.login = this.nickname;
        this.user.avatar_id = this.selectedAvatar.id;
        this.changeState(this.STATE.MENU);
      } else {
        alert(this.trans('error_save_data'));
        if (response?.result?.status === undefined) {
        } else {
          this.isValidNickname = false;
        }
      }
      //
      this.hideSpinner();
      this.isLoadingSaveData = false;
    },
    saveUserData() {
      return new Promise(async (resolver) => {
        try {
          const str = [this.chatId + this.nickname + this.selectedAvatar.id + SALT].join('');
          const secure = SHA256(str).toString();

          const {data} = await axios.post(
              CONSTANTS.API_URL + "/user/update", {
                chat_id: this.chatId,
                nickname: this.nickname,
                avatar_id: this.selectedAvatar.id,
                secure: secure
              });

          resolver(data);

        } catch (e) {
          resolver(false);
        }
      });

    },
    isValidForm() {
      return (
          this.isValidNickname &&
          this.nickname.length > 4
      );
    },
    loadUserData() {
      return new Promise(async (resolver) => {
        const user = await this.findOrCreate();
        if (!user) {
          alert(this.trans('error_get_data'));
          resolver(true);
          return;
        }
        this.user = user.result;
        resolver(true);
      });
    },
    loadAvatars() {
      return new Promise(async (resolver) => {
        const data = await this.getAvatars();
        if (!data) {
          resolver(false);
          return;
        }
        const avatars = data?.result;

        for (let i = 0; i < avatars.length; i++) {
          const avatar = avatars[i];
          avatar.index = i;
          this.avatars.push(avatar);
        }

        if (this.avatars?.length) {
          this.selectedAvatar = this.avatars[0];
        }

        resolver(true);
      });
    },
