        alert(this.trans('error_is_blocked'));
        return;
      }

      //Audio
      this.initAudio();

      //Init avatar
      this.initAvatar();

      //Init selected table
      this.selectedTableId = parseInt(this.tableId || 1);

      //,    
      if (!this.user.login) {
        //     nickname
        this.goToNickname();
      } else {
        //    
        this.goToMenu();
      }
