    initAudio() {
      // 
      this.audioMenuChill = new Audio('./assets/music/chill_2.m4a');
      this.audioMenuChill.loop = true;
      this.audioMenuChill.volume = 0.1;

      // 
      this.audioGame = new Audio('./assets/music/game.m4a');
      this.audioGame.loop = true;
      this.audioGame.volume = 0.1;

      // 
      this.audioDealCards = new Audio('./assets/music/deal_cards.m4a');
      this.audioDealCards.loop = false;
      this.audioDealCards.volume = 0.1;
    },
    changeState(state) {
      switch (state) {
        case this.STATE.LOADING:
          break;
        case this.STATE.RATING:
          break;
        case this.STATE.MENU:
          this.audioGame.pause();
          if (this.musicOn) {
            this.audioMenuChill.play();
          }
          //    
          this.isGameStarted = false;
          break;
        case this.STATE.GAME:
          this.audioMenuChill.pause();
          if (this.musicOn) {
            this.audioGame.play();
          }
          break;
        case this.STATE.NICKANME:
          break;
      }
      this.setState(state);
    },
    debounce(func, timeout) {
      let timer;
      return (...args) => {
        clearTimeout(timer);
        timer = setTimeout(() => {
          func.apply(this, args);
        }, timeout);
      }
    },

