      this.isShowModalFindGame = true;
    },
    hideModalFindGame() {
      //  
      clearInterval(this.intervalTimerFindGame);
      //    
      clearInterval(this.intervalProgressFindGame);
      //  
      this.isShowModalFindGame = false;
    },

    toggleBetBtn() {
      this.isShowBetSettings = !this.isShowBetSettings;
    },

    //  
    leaveRoom() {
      this.removeRoomsEvents();
    },
    //   
    removeRoomsEvents() {
      //   
      if (this.room) {
        if (this.room.leave) {
          this.room.leave();
        }
        if (this.room.removeAllListeners) {
          this.room.removeAllListeners();
        }
      }

      //   
      if (this.roomChat) {
        if (this.roomChat.leave) {
          this.roomChat.leave();
        }
        if (this.roomChat.removeAllListeners) {
          this.roomChat.removeAllListeners();
        }
      }
    },
    onChangeActor() {
      this.isShowBetSettings = false;
      if (this.isMyPlayerActor()) {
        const player = this.getMyPlayer();

        //    bet
        if (this.isMyPlayerActorHasBet()) {

          const currentBet = (this.roomState?.table?.currentBet || 1);

          if (currentBet > player.balance) {
            this.minInputValue = player.balance;
            this.maxInputValue = player.balance;
          } else {
            this.minInputValue = currentBet;
            this.maxInputValue = player.balance;
          }

          //ONLY ONE  ,    
          if (this.inputValue !== this.minInputValue) {
            this.inputValue = this.minInputValue;
          }
        }

        //    raise
        if (this.isMyPlayerActorHasRaise()) {
          const minRaise = this.roomState?.table?.minRaise;
          this.minInputValue = minRaise;
          if (this.roomState?.table?.maxRaise > player.balance) {
            this.maxInputValue = player.balance;
          } else {
            this.maxInputValue = this.roomState?.table?.maxRaise;
          }

          //ONLY ONE  ,    
          if (this.inputValue !== this.minInputValue) {
            this.inputValue = this.minInputValue;
          }

          this.hotKeyRaiseX2 = true;
          this.hotKeyRaiseX2Value = minRaise;

          const oneMinRaise = (minRaise / 2);

          this.hotKeyRaiseX3 = (oneMinRaise * 3 <= player.balance);
          this.hotKeyRaiseX3Value = oneMinRaise * 3;

          this.hotKeyRaiseX4 = (oneMinRaise * 4 <= player.balance);
          this.hotKeyRaiseX4Value = oneMinRaise * 4;

        }
      }
    }
  },
  data() {
    return {
